          allowfullscreen="true"
        >
        </iframe>
      </div>
      <div class="video-box" v-else>
        <img :src="item && item.cover" alt="" />
      </div>
      <!-- <div class="cover-box">
        <el-image
          class="cover"
          :src="item && item.cover"
          fit="contain"
          lazy
        ></el-image>
      </div> -->
      <div class="info">
        <h1 class="title">
          {{ item && (item.full_title || item.title) }}
          <span class="price" v-show="item && !item.has_specs"
            ><em>¥</em> {{ item && item.pay }}</span
          >
        </h1>
        <div :class="['tags', item.has_specs ? 'specs' : '']" v-if="item">
          <template>
            <el-tag class="tag" v-for="key in item.keywords" :key="key">{{
              key
            }}</el-tag>
          </template>
          <div
