            style="text-align: right"
            v-if="item && item.has_specs"
          >
            <el-button
              type="danger"
              round
              plain
              v-for="sp in item.specs"
              :key="sp.title"
              @click="() => (maskVisibility = true)"
              >{{ sp.title }} ¥ {{ sp.price }}</el-button
            >
          </div>
        </div>
      </div>
      <div class="details">
        <p>{{ item && item.description }}</p>
        <p v-html="item && item.detail"></p>
      </div>
      <div :class="['fix-box', { top: maskVisibility }]">
        <div class="fix-inner">
          <p class="head"><strong>❗️淘宝扫一扫❗️</strong></p>
          <el-image
            class="qrcode"
            :src="item && item.pay_img"
            fit="contain"
            lazy
          ></el-image>
          <p>购买模版</p>
          <el-link type="primary" :href="item && item.pay_link" target="_blank"
            >或者点这里</el-link
          >
        </div>
        <el-tag type="success" class="purchase-tag">
          🛒
          <span
            :id="hash"
            class="leancloud_visitors"
            :data-flag-title="item && item.title"
          >
            <i class="leancloud-visitors-count"></i>
          </span>
