      <!-- 相关推荐 -->
      <div class="other-templates">
        <!-- <h3>🎉 推荐</h3> -->
        <template v-if="recommandVisible">
          <div
            class="template-card"
            v-for="temp in recommand"
            :key="temp.id"
            @click.stop="handleTemp(temp)"
          >
            <el-image
              class="small-cover"
              :src="temp.cover"
              fit="scale-down"
              lazy
            ></el-image>
            <h4>
              {{ temp.title }}
              <!-- <span>¥{{ item && item.pay }}</span> -->
            </h4>
          </div>
        </template>
        <Plus :component="true" />
      </div>

      <!-- 会员 -->
      <!-- <div class="fixed-btn">成为会员领取所有模版</div> -->
    </div>
    <!-- mask -->
    <div
      class="mask"
      v-if="maskVisibility"
      @click="() => (maskVisibility = false)"
    >
      <img src="../assets/arrow.png" alt="" />
    </div>
  </div>
</template>
<script>
import Valine from "valine";
import lockMix from "../mixins/lock";
import Plus from "./VIP.vue";
export default {
  mixins: [lockMix],
  components: { Plus },
  data() {
    return {
      id: this.$route.params.id,
      item: null,
      recommandVisible: false,
      maskVisibility: false,
      recommand: [],
    };
  },
  mounted() {
    this.getData();
    this.setComments();
  },
  computed: {
    hash() {
      return window.location.hash;
    },
  },
  methods: {
    getData() {
      const _this = this;
      this.$axios.get(`./picks.json?v=${Math.random()}`).then((res) => {
        let times = res.data;
        let item = times.find((item) => item.id === Number(_this.id));
        let others = times.filter(
          (item) => item.id != _this.id && item.pay && item.hot
        );
        _this.recommand = others;
        if (!item) return _this.openFullScreen2("该模版不存在");
        _this.item = item;
      });
    },
    handleTemp(temp) {
      this.$router.replace({ name: "paid", params: { id: temp.id } });
      this.id = temp.id;
      this.getData();
      this.setComments();
    },
    setComments() {
      new Valine({
        el: "#vcomments",
        // other config
        appId: "wgD3lhLSAPHdLGH0gPy9Ep5n-gzGzoHsz",
        appKey: "DX5XDbJapqKFh8XT5myBkrcc",
        avatar: "hide",
        path: window.location.hash,
        visitor: true,
        recommand: [],
      });
    },
  },
};
</script>
